import React, { Component } from 'react';
import Header from './components/Header';
import AddOption from './components/AddOption';
import Action from './components/Action';
import Options from './components/Options';
import OptionModal from './components/OptionModal';

class IndecisionApp extends Component {
  state = {
    options: [],
    selectedOption: undefined,
  };

  componentDidMount = () => {
    this.getJsonInfo();
  }

    componentDidUpdate = (prevState) => {
      if (prevState.options !== this.state.options.length) {
        const json = JSON.stringify(this.state.options);
        localStorage.setItem('option', json);
      }
    }

      getJsonInfo = () => {
        try {
          const json = localStorage.getItem('option');
          const options = JSON.parse(json);

          if (options) {
            this.setState(() => ({ options }));
          }
        } catch (e) {

        }
      }

  // filter returns new array with filtered options.
  handleDeleteOption = (optionToRemove) => {
    this.setState(prevState => ({
      options: prevState.options.filter(option => option !== optionToRemove),
    }));
  }

    handleDeleteOptions = () => {
      this.setState(() => ({ options: [] }));
    }

    handleSelectedOption = () => {
      this.setState(() => ({ selectedOption: undefined }));
    }

    handleAddOption = (option) => {
      if (!option) {
        return 'Enter valid value to item to add';
      } else if (this.state.options.indexOf(option) > -1) {
        return 'This option already exists';
      }
      return this.setState(prevState => ({ options: prevState.options.concat(option) }));
    }

  handlePick = () => {
    const randomNum = Math.floor(Math.random() * this.state.options.length);
    const randomNumPicked = this.state.options[randomNum];
    this.setState(() => ({ selectedOption: randomNumPicked }));
  }


  render() {
    const title = 'Indecision';

    const subtitle = 'Put your life in the hand of a computer.';

    return (
      <div>
        <Header title={title} subtitle={subtitle} />
        <div className="container">
          <Action
            hasOptions={this.state.options.length > 0}
            handlePick={this.handlePick}
          />
          <div className="widget">
            <Options
              options={this.state.options}
              handleDeleteOptions={this.handleDeleteOptions}
              handleDeleteOption={this.handleDeleteOption}
            />
            <AddOption
              handleAddOption={this.handleAddOption}
            />
          </div>
        </div>
        <OptionModal
          selectedOption={this.state.selectedOption}
          handleSelectedOption={this.handleSelectedOption}
        />
      </div>
    );
  }
}
export default IndecisionApp;



